<template>
    <el-dialog :title="titleShow==true?'添加版本':'编辑版本'" :visible.sync="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
                <el-radio class="radio"  v-model="form.platform" label="1">安卓</el-radio>
            </el-form-item>
            <el-form-item label="渠道:" :label-width="formLabelWidth" v-if="titleShow">
                <el-input v-model="form.dtu" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="版本:" :label-width="formLabelWidth"v-if="titleShow">
                <el-input v-model="form.version" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="描述:" :label-width="formLabelWidth">
                <el-input v-model="form.info" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="选择包:" :label-width="formLabelWidth">
                <el-select v-model="form.pkg_id"
                           filterable
                           clearable
                           placeholder="选择包">
                    <el-option
                            v-for="(item,index) in sourceList"
                            :key="item.id"
                            :label ="item.id"
                            :value="item.id">{{getPkgName(item)}}
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {

        data() {
            return {
                titleShow:false,
                dialogForm: false,
                formLabelWidth: '200px',
                disabled:true,
                isShow:true,
                sourceList:[],
                form:{
                    info: '',
                    id:'',
                    platform:'1',
                    version:'',
                    pkg_id:'',
                }
            }
        },
        mounted() {
            this.getPkgList();
        },
        watch: {
            'form.platform': function () {
                this.getPkgList();
            },
            'form.version': function () {
                this.getPkgList();
            },
            'form.dtu': function () {
                this.getPkgList();
            }
        },
        methods: {
            getPkgList(){
                quRequest.send(quRequest.Apis.qutuiba.version.getPkgList,{
                    platform: 1,
                    version: this.form.version,
                    dtu: this.form.dtu,
                    page: 1,
                    offset:30
                }).then(res => {
                    this.sourceList = res.data.list;
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.loading = false;
                }).done();
            },
            getPkgName: function (item) {
                var url = item.url;
                var match = url.match(/\/([^\/]*)$/);
                return [this.form.platform[item.platform],item.version, item.version_small, (match || [])[1]].join(' - ');
            },
            showInfoDialog(item) {
                if(item){
                    this.isShow = false;
                    this.form.platform= item.platform;
                    this.form.info =item.info;
                    this.form.md5=item.md5;
                    this.form.id =item.id;
                    this.titleShow = false;
                }else{
                    this.titleShow = true;
                    this.form.platform= '1';
                    this.form.version='';
                    this.form.info ='';
                    this.form.pkg_id = '';
                }

                this.dialogForm = true;
            },
            submit(){
                quRequest.send(this.titleShow==true?quRequest.Apis.qutuiba.version.add:quRequest.Apis.qutuiba.version.modify,this.form
                ).then(res => {
                    this.$message.success("提交成功！");
                    this.dialogForm = false;
                    this.$emit('update')
                }).catch(err => {
                    this.$message.error(err.message);
                }).fin(() => {
                    this.loading = false;
                }).done();
            }
        }
    }
</script>


